.serviceBox {
    margin: 30px 0px 50px 0;
    background-color: #fbfbfb;
    position: relative;
    width: 100%;
}
.service-section .flex-item:hover img {
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    transition: 2s;
}
.serviceBox img {
    height:220px;;
}
.serviceContent {
    padding: 0px 15px;
    text-align: left;
    position: absolute;
    top: 0;
    left: 0px;
    width: 93%;
    background: #00000073;
    right: 0;
    bottom: 0;
    color: #fff;
    height: 96%;
}
.serviceContent h2 {
  font-size: 20px;
  font-weight: 500;
  margin: 5px 0 0 0;
  padding-top: 25%;
    padding-left: 5%;
}
.serviceContent p {
    text-align: justify;
    font-size: 14px;
    line-height: 20px;
    margin-top: 10px;
    padding-top: 0%;
    padding-left: 5%;
    width: 80%;
}
.service-section .flex-container {
    display: flex;
    flex-direction: row;
    font-size: 30px;
    text-align: center;
  }
  
 .service-section .flex-item {
    background-color: #fff;
    padding: 10px;
    flex: 50%;
  }
.service-section .heading {
  color: #4353a0;
    padding: 30px 0 0px 0px;
    text-align: center;
    font-size: 30px;
    margin: 0 auto;
    text-transform: uppercase;
}
.service-section {
  background-color: #fff;
  margin-top: -25px ;
  padding: 30px 0;
}


  @media (max-width: 768px) {
    .service-section .flex-container  {
      flex-direction: column;
    }
    .serviceBox { margin-bottom: 0; }
  }