.blog-container {
  background-color: #fff;
}
.blog-container h2 {
  color: #4353a0;
  text-transform: uppercase;
  margin: 0 auto;
    text-align: center;
    font-size: 30px;
    padding-top: 30px;
}

.blog-container .carousel-root {
  width: 64% !important;
  margin: auto !important;
}

.blog-container  .carousel .slide {
  background: #fff !important;
  color: black;
  height: 100%;
  text-align: left!important;
}

.blog-container .carousel .slide img {
  width: 300px !important;
  margin: 8% 0% 0 12%;
  height: 330px;
}

.blog-container .myCarousel {  
  position: relative;
  background: #fafafa7a;
  margin-top: -15%;
  opacity: 2;
  width: 50%;
  margin-left: auto;
  margin-right: 6%;
  padding-top: 6%;
  padding-bottom: 10%;
  padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 300px;
    box-shadow: 0 0px 5px 0 rgb(0 0 0 / 10%), 0 0px 4px 0 rgb(0 0 0 / 10%);
    margin-bottom: 2%;
}

.blog-container .carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: 5% !important;
}

.blog-container .myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.blog-container .myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.blog-container .myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: sans-serif;
  max-height: 67px;
}

.blog-container .myCarousel p:before {
  content: "“";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.blog-container .myCarousel p:after {
  content: "”";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.blog-container .carousel .control-dots .dot {
  box-shadow: none !important;
  background: #454545 !important;
  outline: 0;
}

.blog-container .carousel.carousel-slider .control-arrow {
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

.blog-container .carousel .control-prev.control-arrow:before {
  border-right: 8px solid #000;
  display: none;
}

.blog-container .carousel .control-next.control-arrow:before { 
  border-left: 8px solid #000;
  display: none;
}

.blog-container .carousel .control-arrow, .blog-container .carousel.carousel-slider .control-arrow { opacity: initial;}

.blog-container .App {
  text-align: center;
}

.blog-container .testimonial-heading {
  color: #4353a0;
    padding: 20px 0 10px 0;
    text-align: center;
    font-size: 30px;
    margin: 0 auto 30px;
    text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .blog-container .carousel .slide img {
    width: 80%!important;
    border-radius: 0;
    height: 300px;
}
  .blog-container .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .blog-container   .carousel.carousel-slider .control-arrow {
    display: none !important;
  }
  .blog-container .myCarousel {
    background: #fafafa;
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 269px;
  }

  .blog-container   .carousel .slide img {
    width: 24% !important;
    border-radius: 50%;
  }
  .blog-container  .testimonial-heading {
    color: #4353a0;
    padding: 20px 0 10px 0;
    text-align: center;
    font-size: 30px;
    margin: 0 30% 30px;
    text-transform: uppercase;
}
}